import React from 'react';
import { Switch, HashRouter, Route, Redirect } from 'react-router-dom';

import Common, { CommonContext } from 'components/Common';

import Main from 'container/main';
import Login from 'container/login';
import Register from 'container/register';
import Home from 'container/home';
import Account from 'container/account';
import AccountsTheme from 'container/accountsTheme';
import BatchAdd from 'container/batchAdd';
import Ledgers from 'container/ledgers';

import HTMLElement from 'container/study/htmlElement'

export const Routes = [
    { path: 'main', name: '首页', component: Main, exact: true },
    { path: 'login', name: '登陆', component: Login, exact: true },
    { path: 'register', name: '注册', component: Register, exact: true },
    { path: 'home', name: '用户首页', component: Home, exact: true },
    { path: 'accounts', name: '账户管理页', component: Account, exact: true },
    { path: 'accountsTheme', name: '账户管理页', component: AccountsTheme, exact: true },
    { path: 'ledgers', name: '账本', component: Ledgers, exact: true },
    { path: 'batchAdd', name: '账户批量添加', component: BatchAdd, exact: true },
    { path: 'study', name: '学习', component: HTMLElement, exact: true },
];


export default function () {
    return (
        <CommonContext.Provider value={Common()}>
            <HashRouter>
                <Switch>
                    {
                        Routes.map(o =>
                            <Route
                                key={o.path}
                                exact={o.exact}
                                path={`/${o.path}`}
                                render={
                                    (prop) => {
                                        return <o.component {...prop} Routes={o.children || o.sibling || []} />
                                    }
                                }
                            />
                        )
                    }
                    <Redirect from={'/'} to={'main'} />
                </Switch>
            </HashRouter>
        </CommonContext.Provider>
    );
}