import React, { useEffect, useState, useContext } from 'react';
import { Table } from 'antd';
import styles from './style.module.scss';

export default function HTMLElement() {
    const dict = {
        DOMStringMap: 'https://developer.mozilla.org/zh-CN/docs/Web/API/DOMStringMap',
        DOMString: 'https://developer.mozilla.org/zh-CN/docs/Web/API/DOMString',
        Boolean: 'https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Boolean',
        Element: 'https://developer.mozilla.org/zh-CN/docs/Web/API/Element',
        CSSStyleDeclaration: 'https://developer.mozilla.org/zh-CN/docs/Web/API/CSSStyleDeclaration',
    }
    const data = [
        {
            name: 'HTMLElement.dataset',
            detail: 'https://developer.mozilla.org/zh-CN/docs/orphaned/Web/API/HTMLOrForeignElement/dataset',
            type: '只读',
            eleType: 'DOMStringMap',
            description: '获取元素的自定义属性，是一个对象（key-value，只读）',
        },
        {
            name: 'HTMLElement.dir',
            detail: 'https://developer.mozilla.org/zh-CN/docs/Web/API/HTMLElement/dir',
            type: '',
            eleType: 'DOMString',
            description: '获取/设置元素的方向，可选的值有：ltr，rtl，auto',
        },
        {
            name: 'HTMLElement.hidden',
            detail: 'https://developer.mozilla.org/zh-CN/docs/Web/API/HTMLElement/hidden',
            type: '',
            eleType: 'Boolean',
            description: '获取/设置元素是否隐藏',
        },
        {
            name: 'HTMLElement.offsetHeight',
            detail: 'https://developer.mozilla.org/zh-CN/docs/Web/API/HTMLElement/offsetHeight',
            type: '只读',
            eleType: 'double',
            description: '元素自身可视高度加上上下border的宽度',
        },
        {
            name: 'HTMLElement.offsetLeft',
            detail: 'https://developer.mozilla.org/zh-CN/docs/Web/API/HTMLElement/offsetLeft',
            type: '只读',
            eleType: 'double',
            description: '元素自己border左边距离父元素border左边或者body元素border左边的距离',
        },
        {
            name: 'HTMLElement.offsetParent',
            detail: 'https://developer.mozilla.org/zh-CN/docs/Web/API/HTMLElement/offsetParent',
            type: '只读',
            eleType: 'Element',
            description: '元素的父元素，如果没有就是body元素',
        },
        {
            name: 'HTMLElement.offsetTop',
            detail: 'https://developer.mozilla.org/zh-CN/docs/Web/API/HTMLElement/offsetTop',
            type: '只读',
            eleType: 'double',
            description: '元素自己border顶部距离父元素顶部或者body元素border顶部的距离',
        },
        {
            name: 'HTMLElement.offsetWidth',
            detail: 'https://developer.mozilla.org/zh-CN/docs/Web/API/HTMLElement/offsetWidth',
            type: '只读',
            eleType: 'double',
            description: '元素自身可视宽度加上左右border的宽度',
        },
        {
            name: 'HTMLElement.style',
            detail: 'https://developer.mozilla.org/zh-CN/docs/orphaned/Web/API/ElementCSSInlineStyle/style',
            type: '',
            eleType: 'CSSStyleDeclaration',
            description: '获取/设置元素的style属性',
        },
        {
            name: 'HTMLElement.tabIndex',
            detail: 'https://developer.mozilla.org/zh-CN/docs/orphaned/Web/API/HTMLOrForeignElement/tabIndex',
            type: '',
            eleType: 'long',
            description: '获取/设置元素的tab键控制次序',
        },
        {
            name: 'HTMLElement.title',
            detail: 'https://developer.mozilla.org/zh-CN/docs/Web/API/HTMLElement/title',
            type: '',
            eleType: 'DOMString',
            description: '获取/设置元素的title属性',
        },

    ];
    let dataSource = data.map((item, index) => ({ ...item, key: index + '' }))

    const columns = [
        {
            title: '属性名称',
            dataIndex: 'name',
            key: 'name',
            render(text, record, index) {
                return (
                    <>
                        {record.detail ? <a href={record.detail} title={record.detail}>{text}</a> : text}
                        <span className={styles.badge}>{record.type}</span>
                    </>
                )
            }
        },
        {
            title: '属性类型',
            dataIndex: 'eleType',
            key: 'eleType',
            render(text, record, index) {
                return dict[text] ? <a href={dict[text]} target="_blank">{text}</a> : text;
            }
        },
        {
            title: 'description',
            dataIndex: 'description',
            key: 'description',
        },
    ];
    return (
        <div>
            <h1 style={{ fontSize: 24, fontWeight: 'bold', padding: 16 }}>HTMLElement</h1>
            <p style={{ padding: '0 16px' }}>HTMLElement 接口表示所有的 HTML 元素。一些HTML元素直接实现了HTMLElement接口，其它的间接实现HTMLElement接口.</p>
            <Table
                title={() => {
                    return <>
                        <h2 style={{ fontSize: 24 }}>属性</h2>
                        <p>继承自父接口Element和 GlobalEventHandlers的属性  </p>
                    </>
                }}
                columns={columns}
                dataSource={dataSource}
                pagination={false}
            />
        </div>
    )
}